<template>
  <div id="counter" style="visibility: visible">
    <svg
      id="SvgjsSvg1704"
      width="100%"
      height="100%"
      xmlns="http://www.w3.org/2000/svg"
      version="1.1"
      xmlns:xlink="http://www.w3.org/1999/xlink"
      xmlns:svgjs="http://svgjs.com/svgjs"
      viewBox="0 0 300 300"
    >
      <defs id="SvgjsDefs1705"></defs>
      <circle
        id="SvgjsCircle1706"
        r="133"
        cx="150"
        cy="150"
        fill="#000000"
        fill-opacity="0.8"
        transform="matrix(-1.8369701987210297e-16,-1,1,-1.8369701987210297e-16,2.842170943040401e-14,300)"
      >
      </circle>
      <circle
        id="SvgjsCircle1707"
        r="133"
        cx="150"
        cy="150"
        fill="none"
        stroke="#cc0000"
        stroke-width="20"
        stroke-alignment="outer"
        stroke-dasharray="880"
        stroke-dashoffset="880"
        transform="matrix(-1.8369701987210297e-16,-1,1,-1.8369701987210297e-16,2.842170943040401e-14,300)"
      >
        <animate attributeName="stroke-dashoffset"
                 :from="endCircum"
                 :to="-circum"
                 :dur="originCountDown"/>
      </circle>

      <text
        id="SvgjsText1707"
        font-family="Montserrat"
        font-size="120"
        font-weight="100"
        fill="#ffffff"
        text-anchor="middle"
        family="Montserrat"
        size="120"
        weight="100"
        anchor="middle"
        x="150"
        y="35"
      >
        <tspan id="SvgjsTspan1836" dy="156" x="150">{{seconds}}</tspan>
      </text>
    </svg>
  </div>
</template>

<script>
export default {
  name: "CounterDesktop",
  data(){
    return {
      seconds:30,
      tid: 0,
      endCircum:0,
      originCountDown: 30,
      r: 133
    }
  },
  computed: {
    // cx() { // 中心点位置
    //   return this.size / 2
    // },
    // r() { // 半径
    //   return this.size / 2 - this.width
    // },
    circum() {
      return parseInt(this.r * Math.PI * 2)
    }
  },
  created() {
    const vm = this;
    vm.tid = setInterval(() => {
      vm.seconds--;
      if (vm.seconds <= 0) {
        clearInterval(vm.tid);
        this.$emit('counterDesktopStop')
      }
    }, 1000);
  }
};
</script>

